.button-group {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-end;
    height: 300px;
    padding: 20px;
    position: absolute;
    top: 0;
    right: 0;
}

.colour-theme-button {
    width: 50px;
    height: 50px;
    border-radius: 100%;
    border: 2px solid #ccc;
    background-color: white;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    outline: none;
    transition: background-color 0.3s;
    margin-bottom: 20px;
    position: relative;
    overflow: hidden;
    box-shadow: 4px 4px rgba(0, 0, 0, 0.1);
}

/* Vertical line on left */
.colour-theme-button::before {
    content: '';
    position: absolute;
    top: 0px;
    bottom: 0px;
    left: 25%;
    width: 2px;
    transform: translateX(-50%);
    background-color: #DD0534;
    z-index: 2;
}

.colour-theme-button .horizontal-lines {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    z-index: 1;
}

.horizontal-line {
    width: 100%;
    height: 2px;
}

/* Default colour : horizontal line : top button */
.button-0 .horizontal-line {
    background-color: #D5E3F0;
}

/* Default colour : horizontal line : middle button */
.button-1 .horizontal-line {
    background-color: #e0d2ec;
}

/* Default colour : horizontal line : bottom button */
.button-2 .horizontal-line {
    background-color: #d6ebc5;
}

/* Default colour : border and background : top button */
.button-0 {
    border-color: #31597e;
}

/* Default colour : border and background : middle button */
.button-1 {
    border-color: #340a57;
}

/* Default colour : border and background : bottom button */
.button-2 {
    border-color: #214107;
}

/* Active colour : border and background : top button */
.button-0.active {
    background-color: #D5E3F0;
}

/* Active colour : border and background : middle button */
.button-1.active {
    background-color: #e0d2ec;
}

/* Active colour : border and background : bottom button */
.button-2.active {
    background-color: #d6ebc5;
}

.button-0.active .horizontal-line,
.button-1.active .horizontal-line,
.button-2.active .horizontal-line {
    background-color: white;
}